@import url("https://fonts.googleapis.com/css?family=Poppins");
@import url("https://fonts.googleapis.com/css?family=Lato:700");
*, *::before, *::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

body {
  margin: 0;
  line-height: 1.6;
  font-family: "Poppins", sans-serif;
  background-color: #fcfcfc;
  color: #707070; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Lato", sans-serif;
  margin: 0; }

img {
  max-width: 100%;
  width: 100%;
  height: 100%;
  display: block; }

ul {
  margin: 0;
  padding: 0; }

li {
  list-style: none; }

p {
  margin: 0; }

button:active {
  box-shadow: 0 2px 2px #e4e4e4;
  transform: translateY(2px); }

.l-container {
  max-width: 1200px;
  margin: 0 auto; }

.success {
  margin: 25px auto;
  padding: 16px;
  border: 2px solid #26C489;
  width: 300px;
  color: #26C489;
  text-transform: uppercase;
  text-align: center; }



